"use client";

import { useState, useEffect } from "react";
import ImageTrail from "./ImageTrail";

export default function HeroSection() {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 1024);
    };

    checkMobile();
    window.addEventListener("resize", checkMobile);

    return () => window.removeEventListener("resize", checkMobile);
  }, []);

  const images = [
    "/images/1.png",
    "/images/2.jpg",
    "/images/3.jpg",
    "/images/4.jpg",
  ];

  return (
    <div className="relative w-full min-h-screen bg-slate-900 overflow-hidden">
      {/* Background Layer */}
      {isMobile ? (
        <div className="absolute inset-0 bg-linear-to-br from-purple-600 via-pink-600 to-blue-600 opacity-80" />
      ) : (
        <div className="absolute inset-0 bg-slate-900" />
      )}

      {/* Image Trail Effect - Desktop Only */}
      {!isMobile && <ImageTrail items={images} />}

      {/* Hero Content */}
      <div className="relative z-20 flex flex-col items-center justify-center min-h-screen px-4 sm:px-6 lg:px-8">
        <div className="max-w-4xl mx-auto text-center">
          <h1 className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-white mb-6 leading-tight">
            Create Amazing
            <span className="block bg-linear-to-r from-purple-400 via-pink-400 to-blue-400 bg-clip-text text-transparent">
              Visual Experiences
            </span>
          </h1>

          <p className="text-lg sm:text-xl md:text-2xl text-gray-300 mb-10 max-w-2xl mx-auto">
            Interactive image trails that follow your cursor and create stunning
            visual effects
          </p>

          <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
            <button className="px-8 py-4 bg-linear-to-r from-purple-500 to-pink-500 text-white font-semibold rounded-lg text-lg hover:from-purple-600 hover:to-pink-600 transition-all duration-300 shadow-lg hover:shadow-xl transform hover:scale-105 w-full sm:w-auto">
              Get Started
            </button>

            <button className="px-8 py-4 bg-white bg-opacity-10 backdrop-blur-sm text-white font-semibold rounded-lg text-lg hover:bg-opacity-20 transition-all duration-300 border border-white border-opacity-20 w-full sm:w-auto">
              Learn More
            </button>
          </div>

          {!isMobile && (
            <p className="mt-8 text-sm text-gray-400 animate-pulse">
              Move your cursor to see the magic ✨
            </p>
          )}
        </div>
      </div>
    </div>
  );
}
